.sky {
    position: absolute; top:0;
    height:90%;
    width:100%;
    z-index: 0;
}
.ground {
    position: absolute; bottom:0;
    height:20%;
    width:100%;
    z-index: 5;
}
.mntn1 {
    position: absolute;
    bottom: -22%;
    right: -2%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 3;
    animation-name:raise1;
    animation-duration: 2s;
    animation-iteration-count: 1;
}
@keyframes raise1{
    0% { transform: translateY(2%); }
    100% { transform: translateY(0); }
}
.mntn2 {
    position: absolute;
    bottom: -27%;
    left: -8%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 2;
    animation-name:raise2;
    animation-duration: 2s;
    animation-iteration-count: 1;
}
@keyframes raise2{
    0% { transform: translateY(3%); }
    100% { transform: translateY(0); }
}
.mntn3 {
    position: absolute; bottom:-9%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 1;
    animation-name:raise3;
    animation-duration: 2s;
    animation-iteration-count: 1;
}
@keyframes raise3{
    0% { transform: translateY(4%); }
    100% { transform: translateY(0); }
}
#moon {
    position: absolute;
    top: 6%;
    left: 10%;
    background-size: contain;
    width: 8vh;
    height: 8vh;
    z-index: 5;
    animation-name: moon_anim;
    animation-iteration-count: 1;
    animation-duration: 2s;
}
@keyframes moon_anim {
    0% { transform: translate(-30%, 15%); }
    100% { transform: translate(0%, 0%); }
}
#hut {
    position: absolute;
    left: 32%;
    bottom: 5%;
    width: 36%;
    height: 45%;
    background-size: contain;
    z-index: 6;
}
#hut-light {
    animation-name: blinking;
    animation-iteration-count: 3;
    animation-duration: 3s;
    animation-timing-function: linear;
}
#main-title {
    position: absolute;
    top: 27%;
    color: white;
    opacity: .95;
    left: 10%;
    z-index: 0;
    animation-name: title_anim;
    animation-iteration-count: 1;
    animation-duration: 2s;
}
@keyframes title_anim {
    0% { transform: translateX(4%); }
    100% { transform: translateX(0); }
}
#title {
    font-size: 8vh;
}
#subtitle {
    font-size: 4vh;
}

#note {
    position: fixed;
    font-size: 1.5vh;
    bottom: 1%;
    right: 2%;
    z-index: 10;
    color: white;
    opacity: .5;
}

@keyframes blinking{
    0% { opacity:1 }

    39% { opacity:1 }
    40% { opacity:0 }
    43% { opacity:0 }
    44% { opacity:1 }

    100% { opacity:1 }
}

@media screen and (max-width: 850px) {
    #hut {
        bottom:2%;
        width: 40%;
        left: 30%;
    }
    #title {
        font-size: 8vh;
    }
    #subtitle {
        font-size: 4vh;
    }
    .under-dev {
        font-size: 2vh;
    }
}
@media screen and (max-width: 600px) {
    #hut {
        bottom: 1%;
        width: 44%;
        left: 26%;
    }
    .mntn1 {
        bottom: -24%;
        right: -3%;
    }
    .mntn3 {
        bottom: -15%;
    }
}
@media screen and (max-width: 450px) {
    #hut {
        left: 15%;
        bottom: 2%;
        width: 70%;
    }
    .mntn1 {
        bottom: -24%;
        right: -3%
    }
    .mntn3 {
        bottom: -17%;
    }
    #moon {
        width: 10vw;
        height: 10vw;
    }

    #main-title {
        top: 15%;
        left: 6%;
    }
    #title {
        font-size: 5vh;
    }
    #subtitle {
        font-size: 3vh;
        margin: 1vw;
    }
    .under-dev {
        font-size: 2vh;
    }
}
@media screen and (min-height: 1200px) {
    .mntn3 {
        bottom: -16%;
    }
    #hut {
        bottom:6%
    }
}
@media screen and (min-width: 2400px) {
    .mntn1 {
        right: -19%;
    }
    .mntn2 {
        left: -20%;
    }
    #main-title {
        top: 16%;
    }
    #title {
        font-size: 10vh;
    }
    #subtitle {
        font-size: 5vh;
    }
}