.page2 {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    position:initial;
}

.page-container {
    width: 95%;
    height: 92%;
    display: flex;
    align-content: center;
    align-items: center;
    flex-direction: row;
    overflow-x: hidden;
}
.room1 {
    width:100%; height:100%
}
.section {
    width: 57%;
    height: 100%;
    color: white;
    display: flex;
    position:relative;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
}
.section-title {
    font-size: 5vh;
    font-weight: 500;
}
.section-subtitle {
    margin-top:1vh;
    font-size:2vh;
    font-style: italic;
}
.section-body {
    margin-top:3vh;
    font-size: 2vh;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.section-body::-webkit-scrollbar {
    display: none;
}

#monitor-container {
    width:100%; height:100%;
    overflow: hidden;
    position:absolute;
    top:0; left:0;
    align-items: center;
    transition-duration: 400ms;
    z-index: 10;
}
#monitor-background {
    width:100%; height:100%;
    position:absolute;
    background-color: rgb(200, 200, 200);
    transition-duration: 500ms;
}
#monitor-arrow {
    animation-name: pulse_opacity;
    animation-iteration-count: infinite;
    animation-duration: 2s;
}
.monitor {
    position:absolute;
    width: 88%;
    height: 75%;
    left: 4%;
    top: 6%;
    z-index: 2;
    transition-duration: 250ms;
}
.phone {
    position:absolute;
    top: 11%;
    left: 59%;
    width: 26%;
    height: 84%;
    z-index: 2;
    transition-duration: 250ms;
    transition-delay: 150ms;
}

.clickable-border {
    transition-duration: 150ms;
    animation-name: pulse_opacity;
    animation-iteration-count: infinite;
    animation-duration: 2s;
}
.clickable-fill {
    cursor:pointer
}

#click-on-pc {
    position: absolute;
    bottom: 0%;
    right: 0%;
    font-size: 1.5vh;
    font-style: italic;
}

@keyframes pulse_opacity{
    0% { opacity:.1 }
    50% { opacity:.7 }
    100% { opacity:.1 }
}

@media screen and (max-width: 1200px) {
    .section {
        padding-left: 3%;
    }
}
@media screen and (max-width: 900px) {
    .section {
        height:60%;
    }
}
@media screen and (max-width: 850px) {
    .page-container {
        flex-direction: column;
    }
    .page2 {
        height:fit-content;
    }
    .room1 {height:fit-content}
    .section {
        height:45%;
        margin-top:5%;
        width:90%
    }
    .phone {
        left: 55%;
        width: 30%;
    }
    .monitor {
        width:92%;
        height: 52%;
    }
}
@media screen and (max-width: 600px) {
    #monitor-container {
        padding-bottom: 20%;
    }
    .phone {
        left: 10%;
        top: 3%;
        width: 80%;
        height: 85%;
    }
    .monitor {
        left:-100%
    }
}
@media screen and (max-width: 380px) {
    .phone {
        height: 75%;
        top:8%;
    }
}
