.blank-space {
    width:100%;
    height:30%;
    background-color: black;
}
.App, .page {
    height:100%; width:100%;
    overflow-x:hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.App::-webkit-scrollbar {
    display: none;
}
.page {
    position:relative;
    overflow:hidden;
}
.background-style {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
