@import url('https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap');

body {
  margin: 0;
  font-family: Oxygen;
  height:100vh;
  width:100vw;
  background-color: black;
  overflow-x:hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
body::-webkit-scrollbar {
  display: none;
}
#root {
  height:100%;
  width:100%;
  overflow-x:hidden;
}